import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

const BlogBox = ({ slika }) => {
  const [text] = useState({
    p: ` Poetak dvadesetog veka su obeleila velika pomeranja evropskih naroda
    u Ameriku: nekadanja britanska kolonija otvorila je tada vrata
    hiljadama ljudi - ak 9% stanovnitva Norveke je polo put Amerike u
    kojoj u ovom trenutku ivi 40 miliona stranaca. Na uveno ostrvo Elis
    sa jo poznatijim Kipom slobode (Lady Liberty), pravo na njujorko tle
    su se iskrcali uveni bezdomnici - u stvari raseljena lica i
    kosmopolite - poput Alberta Ajntajna (iz Austrije), Levija trausa
    (iz Nemake, tvorac uvenih levis farmerki),Vladimira Nabokova (iz
    Rusije),`,
  });

  const [text2, setText2] = useState();

  useEffect(() => {
    if (window.screen.availWidth > 980) {
      setText2(() => {
        return text.p.substr(0, 170);
      });
    } else {
      setText2(() => {
        return text.p.substr(0, 590);
      });
    }
  }, [text.p]);

  return (
    <div className="homeBlog__blogBox">
      <img className="homeBlog__img" alt="pro english" src={slika} />
      <div className="homeBlog__content2">
        <Link className="homeBlog__link" to="/">
          DA LI JE AUSTRALIJA NOVA OBEANA ZEMLJA?
        </Link>
        <p className="homeBlog__p">{text2}</p>
        <div className="homeBlog__line2" />
        <span className="homeBlog__span">17. 06. 2019.</span>
        <p className="homeBlog__p2">
          by <span>Branka</span>
        </p>
      </div>
    </div>
  );
};

export default BlogBox;
