import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import PropType from "prop-types";
import parse from "html-react-parser";

// hooks
import useScrollIndicator from "../../hooks/global/useScrollIndicator";

// images
import kengur from "../../../images/kengur.jpg";

const BlogPost = ({ blog, match }) => {
  const [reset] = useState(() => {
    return window.scrollTo(0, 0);
  });
  const { scrollIndicator } = useScrollIndicator();
  const [data, setData] = useState();

  console.log(blog);

  useEffect(() => {
    blog &&
      blog.map((item, index) => {
        if (match.params.blogId === item._id) {
          setData(item);
        }
      });
  }, [blog, match.params.blogId]);

  return (
    <div>
      <div className="blogPost">
        {scrollIndicator()}
        {reset}
        <h1 className="blogPost__h1">{data && data.title}</h1>
        <span className="blogPost__span">
          {/* Beskrajan, plavi krug. U njemu, zvezda. */}
        </span>
        <div className="blogPost__info">
          <p className="blogPost__date">{data && data.created}</p>
          <p className="blogPost__postedBy">
            <span className="blogPost__postedBy--span">Postovala:</span>
            {data && data.postedBy}
          </p>
        </div>
        <div className="blogPost__content">
          <p className="blogPost__content__p">
            {/* <i>
              Da li je san o izobilju, boljem ivotu i srenim ljudima mogue
              ostvariti samo u Americi ili svoj ameriki san moete da doivite
              i na suprotnoj strani Zemljine hemisfere, u arobnoj Australiji?
            </i> */}
            {data && data.description}
          </p>
          <h2 className="blogPost__h2">
            {/* Ovaj tekst je namenjen onima koji razmiljaju o odlasku iz Srbije i
            pitaju se kuda dalje. */}
          </h2>
          <img
            className="blogPost__img"
            src={`/blog/image/${data && data._id}`}
            alt="proenglish"
          />
          {data && parse(data.body)}
          {/* <p className="blogPost__content__p">
            Poetak dvadesetog veka su obeleila velika pomeranja evropskih
            naroda u Ameriku: nekadanja britanska kolonija otvorila je tada
            vrata hiljadama ljudi - ak 9% stanovnitva Norveke je polo put
            Amerike u kojoj u ovom trenutku ivi 40 miliona stranaca. Na uveno
            ostrvo Elis sa jo poznatijim Kipom slobode (Lady Liberty), pravo na
            njujorko tle su se iskrcali uveni bezdomnici - u stvari raseljena
            lica i kosmopolite - poput Alberta Ajntajna (iz Austrije), Levija
            trausa (iz Nemake, tvorac uvenih levis farmerki),Vladimira
            Nabokova (iz Rusije), Oskara de la Rente (iz Dominikanske Republike)
            i naih Nikole Tesle, Milutina Milankovia, Mihajla Pupina. Doli su
            i ostali kako bi ostvarili svoj ameriki san - i uspeli su u tome.
            Njihove ivotne prie i dan danas inspiriu ljude i hrabre ih da se
            upuste u avanturu ostvarivanja svojih snova. Medjutim, kuda sada idu
            Evropljani?
          </p>
          <p className="blogPost__content__p">
            Nama koji potiemo sa Balkana seobe su dobro poznate jo iz vremena
            arnojevia koji su nae pretke poveli iz podruja Kosmeta i
            Metohije ka plodnoj Vojvodini i zelenoj umadiji za vreme
            istorijskih previranja. Danas ivimo u miru i uvek teimo ka boljem
            i elimo vie. Miljenja su razliita: oni koji ostaju tumae
            odlazak kao pametan potez, izdaju otadbine ili uzaludan pokuaj:
            ovek od sebe ne moe pobei i sve svoje probleme nosi sa sobom; s
            druge strane, oni koji su otili sreni su, uglavnom, jer su se
            skuili i konano ive punim pluima. Oni koji se tek spremaju da
            odu, na najveim su mukama.
          </p>
          <p className="blogPost__content__p">
            Prema novijim podacima, oko 2 miliona Srba su naseljeni trajno u
            razliitim zemljama sveta. U tu brojku nisu uraunati oni koji u
            inostranstvu borave privremeno, preko radne vize, niti su tom cifrom
            obuhvaeni studenti. Prosena starost onih koji su otili je 34,7
            godina, dok je prosena starost stanovnitva u Srbiji 42,2 godine.
            Poredjenja radi, procenjeno je da u Srbiji ivi oko 6 miliona ljudi,
            dok se 2 miliona nalazi u Beogradu. To znai da je godinama unazad
            Srbiju napustio ceo jedan Beograd. Slina situacija sa odlascima je
            i u drugim zemljama bive Jugoslavije. Medju jaim dijasporama
            bivih jugoslovenskih zemalja je ona u Australiji koja broji preko
            200.000 Srba, 50.000 onih koji su u Australiju doli iz Bosne i
            Hercegovine, kao i preko 100.000 Hrvata. Sve vie je onih koji se
            opredeljuju za odlazak u Australiju. Zato je to sluaj?
          </p>
          <h3 className="blogPost__h3">
            1. Gradovi sa visokim kvalitetom ivota
          </h3>
          <p className="blogPost__content__p">
            Prema istraivanju koje obradjuje faktore koji doprinose kvalitetu
            ivota, a koje obuhvata 140 svetskih gradova, australijski gradovi
            se nalaze medju 10 najboljih za ivot. Medju njima se nalaze Sidnej
            i Adelajd, a pri vrhu liste godinama unazad stoji Melburn
            zahvaljujui najviim ocenama u oblasti zdravstva, obrazovanja i
            infrastrukture. Regionalna Australija poslednjih godina doivljava
            procvat zahvaljujui velikim ulaganjima koja za cilj imaju
            iskoriavanje punog potencijala i bogatstva Australije.
          </p>
          <h3 className="blogPost__h3">2. Zarada</h3>
          <p className="blogPost__content__p">
            Ekonomska kriza koja je prodrmala Evropu i Ameriku zaobila je
            Australiju. Zapravo, u poslednjih nekoliko godina Australija je
            ostvarila ekonomski rast i A kreditni rejting, kao i nizak dravni
            dug. Trokovi ivota su vei nego u Srbiji, ali prosena mesena
            zarada je preko 4000 evra, to je dovoljno za normalan ivot. Uzmite
            u obzir i to da e svaki radno sposoban lan porodice moi da se
            zaposli i ostvari meseni prihod na slinom nivou.
          </p>
          <h3 className="blogPost__h3">3. Niska stopa nezaposlenosti</h3>
          <p className="blogPost__content__p">
            Australija je u potrazi za kvalifikovanom i vrednom radnom snagom
            koja zemlji omoguava da parira drugim svetskim silama. Pod uslovom
            da imate potrebno obrazovanje, znanje i/ili iskustvo, potraga za
            poslom u Australiji traje kratko bez obzira na vau struku: posla
            ima i za one koji rade fizike poslove, kao i za visokokvalifikovane
            radnike iz sektora kao to su medicina ili inenjerstvo. Australija
            je poznata po svojoj otvorenosti prema drugim narodima i rado
            prihvata razliitost. Multikulturalnost je ponos Australije i njena
            lepota se ogleda u bogatom kulturnom ivotu, hrani, obiajima i
            festivalima koje organizuje preko 270 nacija koje ive u Australiji.
            Zapravo, svaki etvrti Australijanac je rodjen van Australije.
          </p>
          <h3 className="blogPost__h3">4. Stabilnost i bezbednost</h3>
          <p className="blogPost__content__p">
            Iako je Australija poznata po otrovnim vrstama insekata i gmizavaca,
            savremenom oveku najee preti opasnost od drugog oveka. Takve
            bojazni nema u Australiji: stopa kriminala je niska i izlazak na
            ulicu nije rizian kao to je postao u Evropi poslednjih godina.
            Takodje, Australijanci su medju retkim, blagoslovenim, nacijama koje
            ive u miru i bez stresa, ne znajui ko je premijer drave, niti
            koje su vladajue stranke. Za politiku ne ostaje previe vremena u
            zemlji koja vas svakodnevno zavodi svojom prirodom i lepotom. Ko bi
            gledao mimo te lepote u ekrane?
          </p>
          <h3 className="blogPost__h3">5. Najsrenija nacija na svetu</h3>
          <p className="blogPost__content__p">
            Australijanci nose titulu najsrenijih i najraspoloenijih ljudi na
            svetu. Svi prethodno pomenuti faktori svakako doprinose tome, a tu
            je i veliki broj sunanih dana. Mnogo govori i podatak da je
            Australija zemlja sa najduim ivotnim vekom. Prosena starost
            Australijanaca je 84 godine. U Australiji moete da vrite
            fotosintezu, izbacujui negativnu energiju i proslavljajui Novu
            godinu na nekoj od australijskih plaa, na obali okeana, uz rotilj
            i hladno pie, u drutvu ljudi iz celog sveta koji ne sude o drugima
            na osnovu mesta u kome su rodjeni. Umesto zakljuka, jer kako da
            stavimo taku na jednu priu kakva je Australija: ta vi mislite, da
            li smo Australiju sagledali kroz ruiaste naoari ili ima i istine
            u ovome? Gde ete vi, sada kada moemo bilo kuda?
          </p>
          <h5 className="blogPost__signature">Hugs, Tierka B.</h5> */}
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  const { blog } = state;
  return blog;
};

BlogPost.propType = {
  blog: PropType.object
};

export default connect(
  mapStateToProps,
  null
)(withRouter(BlogPost));
